.nav-bar {
    display: flex;
    font-size: 2vh;
    margin: auto;
    float: none;
width: 50%;
  }

  .tab {
      margin: auto;
  }

  .body {
      margin: auto;
      min-height: 100vh;
      background-color: rgba(101, 0, 0, 0.469);
      font-family: 'VT323', monospace;
      //background-image: url("band.png");
      font-size: 200%
      
  }
  
  h2 {
    padding: 5px;
    color: red;
  }


  div {
    margin: auto;
    text-align: center;
    color: lightcyan;
}

li {
    display: block;
    margin: auto;
    font-size:175%
}

li:hover {
    background-color: slateblue
}

ul {
    display: flex;
    flex-direction: row;
    list-style-type: none;
}

a {
    color: lightpink;
    padding: 4px 4px;
}

a:hover {
    padding: 4px 4px;
    background-color: slateblue
}

.tab {
    cursor: pointer;
}


.tab:hover {
    background-color: slateblue;
    opacity: 0.3;
}
  
.currentPage {
    padding: 4px 4px;
    background-color: black;
}

.row {
    display: -ms-flexbox; /* IE 10 */
    display: flex;
    -ms-flex-wrap: wrap; /* IE 10 */
    flex-wrap: wrap;
    padding: 0 4px;
  }
  
  /* Create two equal columns that sits next to each other */
  .column {
    -ms-flex: 50%; /* IE 10 */
    flex: 50%;
    padding: 0 4px;
  }
  
  .column .ModalImage {
    margin-top: 8px;
    vertical-align: middle;
    padding: 10px 10px;
    border-radius: 29px;
    cursor: pointer;
    transition: 0.3s;
    float: left;
    width: 30%;
  }
.column .ModalImage:hover {
      background-color:rgb(78, 0, 0);
      opacity: 0.8;
  }